*{
  user-select: none;
}

html {
  overflow: hidden;
}

.hero.is-success {
  overflow: hidden;
  background-color: #008083;
}

.default{
  font-size: 15pt;
  -webkit-font-smoothing : none;
  font-family: monospace;
  color:black;
  background-color: #c0c0c0;
}

.pop-border-main{
  box-shadow:
  -1px -1px 0px 1px #fffffe, 
  -3px -3px 0px 1px #abc9cb, 
  1px -1px 0px 1px #7e7e7c, 
  -1px 1px 0px 1px #7e7e7c, 
  1px 1px 0px 1px #7e7e7c, 
  -3px 1px 0px 1px #abc9cb,
  1px -3px 0px 1px #abc9cb, 
  2px 2px 0px 1px #010303, 
  0px 0px 0px 4px #010303,
  0 0 7px rgba(0,0,0,.9);
}

.pop-border {
    box-shadow: 
    1px 1px 0px 1px #7e7e7c, 
    3px 3px 0px 1px #010303, 
    -1px 1px 0px 1px #fffffe, 
    -1px 3px 0px 1px #010303, 
    1px -1px 0px 1px #fffffe, 
    3px -1px 0px 1px #010303, 
    -1px -1px 0px 1px #fffffe;
}

.in-border-hard{
  background-color: #dfdfdf;

  box-shadow: 
    1px 1px 0px 1px #d7d7d6, 
    3px 3px 0px 1px #ececec, 
    -1px 1px 0px 1px #5a5a59, 
    -1px 3px 0px 1px #ececec, 
    1px -1px 0px 1px #5a5a59, 
    3px -1px 0px 1px #ececec, 
    -1px -1px 0px 1px #5a5a59;
}

.in-border-soft{
  box-shadow:
  -1px -1px 0px 1px #9f9ca0, 
  1px 1px 0px 1px #dddddd;
}


.hidden{
  display:none;
}